:root {
  --button-background-color: rgba(255, 255, 255, 0.87);
  --button-border-color: rgba(0, 0, 0, 0.54);
  --button-text-color: rgba(0, 0, 0, 0.87);
}

html,
body,
canvas {
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.tools {
  position: absolute;
  top: 0;
  left: 0;
  margin: 5px;
}

.tool-button {
  background-color: var(--button-background-color);
  margin: 5px;
  border-style: none;
  border: 1px solid var(--button-border-color);
  color: var(--button-text-color);
}
